import React from 'react'
import SlideItem from "../components/SlideItem";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from 'react-slick';
import '../styles/mainSlider.css'

export default function MainSlider() {
    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        arrows: true
    }
    return (
        <Slider {...settings}>
            <SlideItem
                backgroundImage="/qkzclan/images/slide1.png"
                alt="slide"
                linkText="PC GAMEPLAY"
                titleText="POWERGUIDO ORIGINALS VS FROGBOT"
            />
            <SlideItem
                backgroundImage="/qkzclan/images/slide1.png"
                alt="slide"
                linkText="PC GAMEPLAY"
                titleText="POWERGUIDO ORIGINALS VS FROGBOT"
            />
            <SlideItem
                backgroundImage="/qkzclan/images/slide1.png"
                alt="slide"
                linkText="PC GAMEPLAY"
                titleText="POWERGUIDO ORIGINALS VS FROGBOT"
            />
            <SlideItem
                backgroundImage="/qkzclan/images/slide1.png"
                alt="slide"
                linkText="PC GAMEPLAY"
                titleText="POWERGUIDO ORIGINALS VS FROGBOT"
            />
        </Slider>
    )
}
